import React from 'react';
import Loader from './Loader';

const ViewPortfolio = ({portfolio}) => {
    const showPortfolio = portfolio.length ? (
        portfolio.map((p, idx) => {
            let flex = "row";
            if (idx % 2 === 0) {
                flex = "row-reverse";
            }

            if (idx === 0) {
                flex += " first";
            }
            return (
                <a href={p.url} key={p.id} className="">
                    <span style={{display: "block"}}>
                        <div className={"portfolio-container-2 " + flex}>
                            <div className="portfolio-info">
                                <span className="portfolio-title">{p.title}</span>
                                <span className="portfolio-description">{p.description}</span>
                            </div>
                            <img className="portfolio-image box-hover box-shadow-2" src={p.imageUrl} alt={p.title}/>
                        </div>
                    </span>
                </a>
                
            );
        })
    ) : (
        <Loader/>
    );
    return (
        <div className="scrollable portfolio-wrapper">
            {showPortfolio}
        </div>
    );
}

export default ViewPortfolio